import "./style.css";
import { loadTossPayments, ANONYMOUS } from "@tosspayments/tosspayments-sdk";

const amount = { currency: "KRW", value: 50_000 };

const main = async () => {
  try {
    const tossPayments = await loadTossPayments(
      "test_ck_DnyRpQWGrNbdwvRLRnqLrKwv1M9E"
    );
    const widgets = tossPayments.widgets({ customerKey: ANONYMOUS });

    await widgets.setAmount(amount);

    await Promise.all([
      widgets.renderPaymentMethods({
        selector: "#payment-method",
        variantKey: "DEFAULT",
      }),
      widgets.renderAgreement({
        selector: "#agreement",
        variantKey: "AGREEMENT",
      }),
    ]);

    const paymentRequestButton = document.getElementById(
      "payment-request-button"
    );

    paymentRequestButton.addEventListener("click", async () => {
      try {
        await widgets.requestPayment({
          orderId: generateRandomString(),
          orderName: "토스 티셔츠 외 2건",
          successUrl: window.location.origin + "/sandbox/success",
          failUrl: window.location.origin + "/sandbox/fail",
          customerEmail: "customer123@gmail.com",
          customerName: "김토스",
          customerMobilePhone: "01012341234",
        });
      } catch (err) {
        console.error("Payment request failed:", err);
        alert("결제 요청에 실패했습니다. 다시 시도해주세요.");
      }
    });
  } catch (err) {
    console.error("Error loading Toss Payments widget:", err);
    alert("결제 위젯 로딩에 실패했습니다. 다시 시도해주세요.");
  }
};

const generateRandomString = () => {
  return crypto
    .getRandomValues(new Uint8Array(20))
    .toString("base64")
    .slice(0, 20);
};

main();
